import React from 'react';

class ChangingCaption extends React.Component {

    captions = ["tacos", "dumplings", "noodles", "Corbin :)", "mama!"]

    constructor(props) {
      super(props);
      this.state = {date: new Date()};
    }

    componentDidMount() {
        this.timerID = setInterval(
          () => this.tick(),
          1000
        );
    }
    
    componentWillUnmount() {
        clearInterval(this.timerID);
    }
    
    tick() {
        this.setState({
          date: new Date()
        });
    }

    render() {
      return (
        <div>
          <h3>Likes {this.captions[this.state.date.getUTCSeconds() % 5]}
          </h3>
        </div>
      );
    }
  }

  export default ChangingCaption;