import React from 'react';
import HomepageImage from './HomepageImage.js'

class ItemBox extends React.Component {
    constructor(props){
        super(props);
        this.description = 
            <div> 
                {/* 
                <h3>{props.projects[{props.id}].title}</h3>
                <p>{props.projects[{props.id}].title}</p> */}
            </div>;
    }

    render() {
      return (
        <div class="boxed">
          <HomepageImage />
          {this.description}
        </div>
      );
    }
  }

  export default ItemBox;